* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}


.home {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  background-image: url('./AI.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 70px;
  font-weight: 900;
}
.navbartxt {
  font-weight: bold;
  
}
.navStyle { 
  color: rgb(16, 17, 64);
  margin: 0px 0px 0px 0px;
  cursor: "pointer";
  border: "2px solid #b1b1b1";
  border-radius: "8px";
  padding: "0px 10px 0px 10px";
  background-color: "green";
}

.headeer {
  background-color: rgb(88, 80, 233);  
  height: 100px;
}
.navbar {
  background-color: rgb(88, 80, 233);  
  height: 75px;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

hr.class-1 {
  border-top: 10px solid rgb(13, 0, 253)
}

hr.class-2 {
  border-top: 3px double rgb(0, 68, 253)
}

hr.class-3 {
  border-top: 1px dashed rgb(0, 68, 253)
}

hr.class-4 {
  border-top: 1px dotted rgb(0, 68, 253)
}

hr.class-5 {
  background-color: #fff;
  border-top: 2px dashed rgb(0, 68, 253)
}

hr.class-6 {
  background-color: #fff;
  border-top: 5px dotted rgb(0, 0, 0)
}


.h5style {
  font-weight: "bold";
  color: "blue";
}
.tablecss {
  width: "600pt";
  border: "1px solid #444444";
}
trcss {
  text-align: "center"; 
  border: "1px solid #aaa";
}
.hrstyle {
  height: "10pt";
  background-color: "blue";
}
.red {
  color: "red";
}
